.pitch-anim-container span{
    display: inline-block;
    white-space: pre;
    opacity: 0;
    animation-name: pitch-animation;
    animation-duration: 8s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);

}

span:nth-child(1) {
    animation-delay: 0.1s;
}
span:nth-child(2) {
    animation-delay: 0.2s;
}
span:nth-child(3) {
    animation-delay: 0.3s;
}
span:nth-child(4) {
    animation-delay: 0.4s;
}
span:nth-child(5) {
    animation-delay: 0.5s;
}
span:nth-child(6) {
    animation-delay: 0.6s;
}
span:nth-child(7) {
    animation-delay: 0.7s;
}
span:nth-child(8) {
    animation-delay: 0.8s;
}
span:nth-child(9) {
    animation-delay: 0.9s;
}
span:nth-child(10) {
    animation-delay: 1s;
}
span:nth-child(11) {
    animation-delay: 1.1s;
}
span:nth-child(12) {
    animation-delay: 1.2s;
}
span:nth-child(13) {
    animation-delay: 1.3s;
}
span:nth-child(14) {
    animation-delay: 1.4s;
}
span:nth-child(15) {
    animation-delay: 1.5s;
}
span:nth-child(16) {
    animation-delay: 1.6s;
}
span:nth-child(17) {
    animation-delay: 1.7s;
}



@keyframes pitch-animation {
    0%{ opacity: 0; transform: translateY(-100px) skewY(10deg) skewX(10deg) rotateZ(30deg); filter: blur(10px); }
    25%{ opacity: 1; transform: translateY(0px) skewY(0deg) skewX(0deg) rotateZ(0deg); filter: blur(0px); }
    75%{ opacity: 1; transform: translateY(0px) skewY(0deg) skewX(0deg) rotateZ(0deg); filter: blur(0px); }
    100%{ opacity: 0; transform: translateY(-100px) skewY(10deg) skewX(10deg) rotateZ(30deg); filter: blur(10px); }
}